.product-endless-wrapper {
    display: flex;
    flex-direction: column;
    gap: 16px;
    margin-top: 20px;
}

.product-endless-top-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.pe-total-left {
    font-weight: 700;
    font-size: 16px;
}

.pe-sorting-right {
    /* WooCommerce dropdown styling */
}

.product-endless-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 16px;
}

.product-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    border: 1px solid #eee;
    border-radius: 4px;
    padding: 8px;
    background: #fff;
    text-decoration: none;
    color: #000;
    transition: transform 0.2s;
}

.product-card:hover {
    transform: scale(1.02);
    background: #f9f9f9;
}

.product-card img {
    width: 100%;
    height: 150px;
    object-fit: cover;
    border-radius: 4px;
    margin-bottom: 8px;
}

.loader {
    grid-column: 1 / -1;
    text-align: center;
    padding: 12px;
    font-weight: 700;
    color: #555;
}

/* Responsive */
@media (max-width: 1024px) {
    .product-endless-grid { grid-template-columns: repeat(2, 1fr); }
}
@media (max-width: 600px) {
    .product-endless-grid { grid-template-columns: 1fr; }
}



/* Add to Cart button styling */
.product-card {
  position: relative;
  display: block;
  text-align: center;
  transition: transform 0.2s;
}
.product-card:hover {
  transform: scale(1.02);
}

.product-card img {
  width: 100%;
  height: 150px;
  object-fit: cover;
  border-radius: 4px;
}

.add-to-cart-btn {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  display: none;
  background: #007cba;
  color: #fff;
  border: none;
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}

.product-card:hover .add-to-cart-btn {
  display: inline-block;
}
